import React, { useState } from "react";
import "./slider.css";

export const SimpleSlider = ({ images }) => {
  const [imageSelected, setImageSelected] = useState(0);
  const prev = () => {
    //El contador no deberá ser menor que 0
    imageSelected === 0
      ? setImageSelected(0)
      : setImageSelected(imageSelected - 1);
  };
  const next = () => {
    //El contador no deberá ser mayor o igual que el tamaño del array de imágenes
    imageSelected === images.length - 1
      ? setImageSelected(images.length - 1)
      : setImageSelected(imageSelected + 1);
  };
  return (
    <div className="slider">
      <img className="main-image fade" src={images[imageSelected]} alt="" />
      <div className="buttons">
        <button id="prev" onClick={prev}>
          <i className="fa fa-chevron-left fa-2x" />
        </button>
        <button id="next" onClick={next}>
          <i className="fa fa-chevron-right fa-2x" />
        </button>
      </div>
    </div>
  );
};
